@use "sass:math";
@import "common/mixins";

$fontFamily: Montserrat;
$fontSize: 18;
$lineHeight: 24;
$minFontSize: 16;
$background: #fff;
$mainTextColor: #000;

$minWidth: 320;
$maxWidth: 1920;
$maxWidthContainer: 1920;
$containerPadding: 114 * 2;
$containerWidth: $maxWidthContainer + $containerPadding;
// 1 no breakpoins
// 2 with breakpoints
$responsiveType: 1;
$pc: $maxWidthContainer + 12;
$tablet: 991.98; // tablets, phones horizontally
$phone: 767.98; // L phones
$smallPhone: 479.98; // S phones

$mainBlue: #597fae;

html {
    scroll-behavior: smooth;
}
body {
    font-family: $fontFamily;
    @include avalue("font-size", $fontSize, $minFontSize);
    line-height: math.div($lineHeight, $fontSize);
    font-weight: 300;
    color: $mainTextColor;
    background-color: $background;
    &._lock {
        overflow: hidden;
    }
}
._lock {
    &._active {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        overflow: hidden;
        background-color: rgba(0, 0, 0, .8);
        z-index: 100;
    }
}
.wrapper {
    width: 100%;
    min-height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    &._loaded {
    }
    > main {
        // footer to the floor
        flex: 1 1 auto;
    }
    // fixing sliders
    > * {
        min-width: 0;
    }
}
._container {
    max-width: $maxWidthContainer + 15 + px;
    margin: 0 auto;
    padding: 0 15px;
    // @media (max-width: $md1 + px) {
    //     max-width: 970px;
    // }
    // @media (max-width: $md2 + px) {
    //     max-width: 750px;
    // }
    // @media (max-width: $md3 + px) {
    //     max-width: none;
    //     padding: 0 10px;
    // }
}

@if ($responsiveType == 1) {
    [class*="__container"]/*,
    [class*="-container"]*/ {
        @if ($maxWidthContainer > 0) {
            max-width: $maxWidthContainer + px;
            // box-sizing: content-box;
            margin: 0 auto;
        }
        @if ($containerPadding > 0) {
            padding: 0 math.div($containerPadding, 2) + px;
            @media (max-width: 1700px) {
                padding: 0 18px;
            }
        }
    }
} @else if ($responsiveType == 2) {
    [class*="__container"],
    [class*="-container"] {
        margin: 0 auto;
        @if ($maxWidthContainer > 0) {
            max-width: $maxWidthContainer;
            // box-sizing: content-box;
        } @else {
            @if ($containerPadding > 0) {
                padding: 0 math.div($container-padding, 2) + px;
            }
        }
        @media (max-width: $pc + px) {
            max-width: 970px;
        }
        @media (max-width: $tablet + px) {
            max-width: 750px;
        }
        @media (max-width: $phone + px) {
            max-width: none;
            @if ($containerPadding > 0 and $maxWidthContainer > 0) {
                padding: 0 math.div($containerPadding, 2) + px;
            }
        }
    }
} @else {
    @error "Unknown responsiveType: '" + $responsiveType + "'";
}
.page {
    flex: 1 1 auto;
    flex-direction: column;
    @media (max-width: $phone + px) {
    }
}
._page-section {
    @include avalue("margin-top", 60, 30);
    @include avalue("margin-bottom", 60, 30);
}
.popups {
    display: none;
}
