.comparison {
    @include avalue('padding-top', 80, 0);
    @include aminvalue("margin-bottom", 60, 30, 768);
    // .comparison__container
    &__container {
        @media (min-width: 1901px) {
            padding: 0 114px;
        }
    }
    // .comparison__subtitle
    &__subtitle {
        @include aminvalue("font-size", 38, 24, 768);
        line-height: math.div(47, 37);
        font-weight: 300;
        margin-bottom: 30px;
        text-align: center;
        .phone-break {
            @media (max-width: 1024px) {
                display: block;
            }
        }
        @media (max-width: 768px) {
            margin-top: 60px;
        }
    }
    // .comparison__title
    &__title {
        .phone-break {
            @media (max-width: 1024px) {
                display: block;
            }
        }
    }
    // .comparison__slider
    &__slider {
        overflow: hidden;
        @media (max-width: 480px) {
            width: calc(100% + 18px * 2);
            margin: 0 -18px;
        }
    }
    .swiper-slide {
        display: flex;
        border-radius: 29px;
        flex: 0 0 100%;
        overflow: hidden;
        @media (min-width: 1025px) {
            flex: 0 0 calc(50% - #{math.div(51, 2)}px);
            margin-right: 51px;
        }
        @media (max-width: 480px) {
            border-radius: 10px;
        }
    }
    &__before-image,
    &__after-image {
        flex: 0 0 50%;
        padding-top: pc(476, 820);
        position: relative;
        ._adaptive__image {
            user-select: none;
            cursor: grab;
        }
    }
    // .comparison__before-image
    &__before-image {
        .comparison__image-caption {
            left: 35px;
            @media (max-width: 1280px) {
                left: 0;
                border-radius: 0 6px 6px 0;
            }
        }
    }
    // .comparison__after-image
    &__after-image {
        .comparison__image-caption {
            right: 35px;
            @media (max-width: 1280px) {
                right: 0;
                border-radius: 6px 0 0 6px;
            }
        }
    }
    // .comparison__image-caption
    &__image-caption {
        position: absolute;
        background-color: rgba(#fff, 0.74);
        border-radius: 6px;
        font-weight: 300;
        text-align: center;
        top: 29px;
        padding: 9px 21px;
        pointer-events: none;
        .em,
        em {
            font-weight: 600;
        }
        @media (max-width: 480px) {
            top: 10px;
        }
        @media (max-width: 600px) {
            .noun {
                display: none;
            }
        }
    }
}
