.appointment {
    @include aminvalue("margin-top", 60, 30, 768);
    @include aminvalue("margin-bottom", 120, 60, 768);
    // .appointment__container
    &__container {
        display: flex;
        padding: 66px 83px 0;
        border-radius: 40px;
        column-gap: pc(52, $maxWidthContainer - $containerPadding * 2);
        background: no-repeat center / 100% 100% url(@img/appointment/appointment-bg.jpg);
        @media (max-width: 1920px) {
            margin-left: 20px;
            margin-right: 20px;
        }
        @media (max-width: 1700px) {
            // or else overwritten by [class*=__container]
            padding: 50px 60px 0;
        }
        @media (max-width: 1440px) {
            padding: 40px 30px 0;
        }
        @media (max-width: 768px) {
            flex-direction: column;
            padding: 60px 20px 0;
            margin-left: 0;
            margin-right: 0;
            margin-top: 20px;
        }
        @media (max-width: 480px) {
            padding: 40px 10px 0;
            background-image: none;
            background-color: #F3F4F8;
            border-radius: 0;
        }
    }
    // .appointment__text-block
    &__text-block {
        padding-bottom: 63px;
        flex-grow: 1;
        @media (min-width: 1700.02px) {
            background: no-repeat right bottom / contain url(@img/doctor/doctor-in-blue.png);
        }
        @media (max-width: 1280px) {
            padding-bottom: 30px;
        }
        @media (max-width: 768px) {
            padding-bottom: 0;
        }
    }
    &__text-content {
        max-width: 740px;
        @media (max-width: 1700px) {
            max-width: none;
        }
        .appointment__contacts {
            margin-top: 0;
        }
        .appointment__social-list {
        }
        .appointment__social-list-item {
            padding-left: 0;
            &.contact-list__item {
                svg {
                    @include size(47);
                }
            }
        }
    }

    // .appointment__title
    &__title {
        margin-bottom: 34px;
    }
    // .appointment__text
    &__text {
        margin-bottom: 38px;
        .row-first {
            @media (max-width: 768px) {
                text-align: center;
            }
        }
        .row-second {
            @media (max-width: 768px) {
                display: flex;
                gap: 30px;
            }
            @media (max-width: 700px) {
                flex-direction: column;
                gap: 50px;
            }
        }
    }
    // .appointment__contacts
    &__contacts {
        display: flex;
        gap: 70px;
        @media (max-width: 1440px) {
            margin-top: 60px;
            flex-direction: column;
            gap: 20px;
        }
        @media (max-width: 768px) {
            flex-direction: column;
            flex: 0 0 50%;
        }
        @media (max-width: 700px) {
            flex-direction: row;
            gap: 30px 60px;
            flex-wrap: wrap;
            justify-content: center;
            text-align: center;
        }
        @media (max-width: 700px) {
            text-align: center;
        }
    }
    // .appointment__contacts-phone
    &__contacts-phone {
    }
    &__contacts-phone-text {
    }
    &__contacts-phone-link {
    }
    // .appointment__contacts-socials
    &__contacts-socials {
    }
    &__contacts-socials-text {
    }
    &__social-list {
    }

    // .appointment__form
    &__form {
        flex: 0 0 434px;
        text-align: center;
        padding-bottom: 63px;
        position: relative;
        @media (max-width: 1700px) {
            // background: no-repeat right bottom / contain url(@img/appointment/surgeon.png);
            flex-basis: 600px;
        }
        @media (max-width: 1280px) {
            flex-basis: 50%;
            padding-bottom: 30px;
        }
        @media (max-width: 768px) {
            max-width: 583px;
            margin-left: auto;
            margin-right: auto;
        }
        @media (max-width: 768px) {
        }
        form {
            display: flex;
            flex-direction: column;
            row-gap: 24px;
            position: relative;
            z-index: 5;
            opacity: 0.8;
        }
    }
    &__form-button {
        // margin-bottom: 24px;
        @media (max-width: 480px) {
            font-size: 16px;
        }
    }
    &__bg-image {
        display: none;
        @media (max-width: 1700px) {
            display: block;
            position: absolute;
            bottom: 0;
            right: 0;
            opacity: 0.2;
            width: 100%;
            max-height: 100%;
            object-fit: contain;
        }
    }
    // .appointment__form-text
    &__form-text {
        margin-bottom: 50px;
        font-size: 24px;
        line-height: math.div(32, 24);
        font-weight: 400;
        margin-bottom: 27px;
        span.em,
        em {
            font-weight: 700;
        }
        @media (max-width: 768px) {
            margin-bottom: 0;
        }
    }
    &__form-error-message {
        color: red;
        min-height: 25px;
        margin-left: 35px;
        font-weight: 500;
        padding: 10px 0;
    }
    &__form-name,
    &__form-phone,
    .dynamic-select {
        display: block;
        width: 100%;
        background-color: #fff;
        border-radius: 47px;
        height: 95px;
        padding: 35px 20px 30px 44px;
        font-size: 20px;
        line-height: math.div(24, 20);
        text-align: left;
        &::placeholder,
        .selected-option[value=""] {
            color: #a8a8a8;
            font-size: inherit;
        }
        .selected-option {
            border-radius: 47px;
        }
        .options {
            border-radius: 47px;
            &-open {
                .option {
                    &:not(:last-child) {
                        border-bottom: 1px solid #a8a8a8;
                    }
                }
            }
            @media (max-width: 320px) {
                border-radius: 30px;
            }
        }
        .option {
            background-color: #fff;
            padding: 35px 20px 30px 44px;
            height: 93px;
            &[data-selected="true"] {
                background-color: $mainBlue;
                color: #fff;
            }
            @media (max-width: 320px) {
                height: 60px;
            }
        }
        @media (max-width: 480px) {
            font-size: 16px;
        }
        @media (max-width: 320px) {
            font-size: 14px;
            padding: 15px 10px 15px 25px;
            height: 60px;
            border-radius: 30px;
        }
    }
    // .appointment__form-name
    &__form-name {
    }
    // .appointment__form-phone
    &__form-phone {
    }
    &__form-row {
        text-align: left;
        input[type="checkbox"] {
            width: 0;
            height: 0;
        }
        & input, & .dynamic-select {
            border: 1px solid transparent;
            transition: border-color .3s;
        }
        &._error input, &._error .dynamic-select {
            border-color: red;
        }
        &._valid input, &._valid .dynamic-select {
            border-color: green;
        }
        label {
            cursor: pointer;
            display: flex;
            align-items: center;
            gap: 20px;
            font-size: 16px;
            line-height: math.div(20, 16);
            @media (max-width: 480px) {
                font-size: 14px;
                line-height: 1.5;
            }
        }
        .checkbox {
            @include size(24);
            background-color: $mainBlue;
            color: #fff;
            border-radius: 6px;
            @include flex-center;
            flex-shrink: 0;
        }
        input:checked ~ .checkbox {
            &::before {
                content: "\e901";
                @extend %ic;
                color: #fff;
            }
        }
    }
    &__form-submit {
        overflow-x: #fff;
        background-color: $mainBlue;
        border-radius: 100px;
    }
    // .appointment__form-message
    &__form-message {
    }
}
