.about-doctor {
    background: linear--gradient(#fff, #f7f7f7);
    @media (max-width: 500px) {
        padding-bottom: 30px;
    }
    // .about-doctor__container
    &__container {
        display: flex;
    }
    &__text-header {
        text-align: left;
        margin-bottom: 50px;
        margin-right: -200px;
        @media (max-width: 1440px) {
            width: 100%;
            text-align: center;
            margin-right: 0;
        }
        @media (max-width: 768px) {
            margin-bottom: 30px;
        }
    }
    &__title {
        text-align: inherit;
        margin-bottom: 19px;
    }
    &__subtitle {
        font-size: 24px;
        @media (max-width: 768px) {
            font-weight: 300;
            font-size: 18px;
            em, .em {
                font-weight: inherit;
            }
        }
    }
    // .about-doctor__text
    &__text {
        text-align: left;
        position: relative;
        z-index: 5;
        padding-right: pc(850, 1920 - 114 * 2);
        @media (max-width: 1440px) {
            padding-bottom: 50px;
        }
        @media (max-width: 1279px) {
            padding-right: 0;
            display: flex;
            padding-bottom: 30px;
        }
    }
    &__wider-element {
        @media (min-width: 1280px) {
            margin-right: -100px;
        }
    }
    .text-content {
        @media (max-width: 1279px) {
            display: flex;
            flex-direction: column;
        }
        .text-content-contacts {
            display: flex;
            align-items: center;
            gap: 30px;
            svg {
                @include size(69);
            }
        }
        ul {
            li {
                &::before {
                    content: "\e906";
                    @extend %ic;
                    color: $mainBlue;
                    font-size: 20px;
                    background: none;
                    border-radius: 0;
                    border: none;
                    @media (max-width: 700px) {
                        position: absolute;
                        top: 15px;
                    }
                }
            }
        }
    }

    // .about-doctor__about-doctor
    &__content {
        position: relative;
    }
    &__image {
        width: pc(850, 1920 - 114 * 2);
        position: absolute;
        object-fit: contain;
        right: 0;
        bottom: 0;
        max-height: 100%;
        height: auto;
        // max-width: 100%;
        @media (max-width: 1440px) {
            width: 650px;
        }
        @media (max-width: 1279px) {
            position: static;
        }
        @media (max-width: 768px) {
            width: 100%;
        }
    }
    &__doctor-info {
        @media (max-width: 1279px) {
            display: flex;
            justify-content: center;

        }
        @media (max-width: 500px) {
            display: flex;
            flex-direction: column;
        }
        .text-content-contacts {
            @media (max-width: 1279px) {
                display: flex;
                flex-direction: column;
                margin-right: auto;
                gap: 30px;
                position: absolute;
                align-items: flex-start;
                left: 0;
            }
            @media (max-width: 480px) {
                position: static;
                gap: 20px;
                svg {
                    @include size(47);
                }
            }
        }
    }
    ul.about-doctor-contact-list {
        @media (max-width: 500px) {
            // flex-direction: column;
        }
    }
    &__doctor-desc {
        position: relative;
        z-index: 7;
        max-width: 1089px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-end;
        margin-bottom: 50px;
        @media (max-width: 1440px) {
            position: absolute;
            bottom: 40px;
            right: 0;
            margin-bottom: 0;
            max-width: 650px;
        }
        @media (max-width: 1279px) {
            right: auto;
            left: 50%;
            transform: translate(-50%, 0);
        }
        @media (max-width: 768px) {
            width: 90%;
            left: 50%;
            transform: translateX(-50%);
        }
        @media (max-width: 500px) {
            width: 100%;
            top: 100%;
            bottom: auto;
            position: static;
            transform: none;
            order: 3;
        }
        .doctor-info__name {
            @media (max-width: 500px) {
                transform: none;
                span {
                    transform: none;
                }
            }
        }
    }
}
