.about-clinic {
    @include aminvalue("padding-bottom", 97, 66, 768);
    background: #f7f7f7;
    &__container {
        display: flex;
        @include aminvalue("column-gap", 83, 38, 768);
        row-gap: 50px;
        @media (max-width: 1440px) {
            display: grid;
            grid-template: auto / pc(846, 1920 - 114 * 2) auto;
        }
        @media (max-width: 1024px) {
            row-gap: 30px;
        }
        @media (max-width: 850px) {
            grid-template-columns: auto;
        }
    }
    &__header {
        @media (max-width: 1440px) {
            grid-column: span 2;
        }
        @media (max-width: 850px) {
            grid-column: span 1;
        }
    }
    &__slider {
        flex: 0 0 pc(846, 1920 - 114 * 2);
        .main-slider {
            margin-bottom: 17px;
            border-radius: 8px;
            overflow: hidden;
            @media (max-width: 1024px) {
                border-radius: 5px;
            }
            .swiper-slide {
                flex: 0 0 100%;
                padding-top: pc(564, 846);
                @media (max-width: 1280px) {
                    border-radius: 8px;
                }
                @media (max-width: 850px) {
                    border-radius: 4px;
                }
            }
        }
        ._lazy-image {
            background-color: rgba(185, 149, 72, .1);
        }
        .spinner {
            border-color: rgba(185, 149, 72, .2);
            border-top-color: transparent;
        }
        .thumbs-slider {
            overflow: hidden;
            .swiper-slide {
                flex: 0 0 calc(20% - #{math.div(16, 5) * 4}px);
                margin-right: 16px;
                padding-top: pc(104, 846);
                cursor: pointer;
                img {
                    outline: 5px solid transparent;
                    outline-offset: -5px;
                    border-radius: 5px;
                    transition: outline-color .3s;
                    background-color: rgba(185, 149, 72, .1);
                }
                @media (max-width: 1440px) {
                    img {
                        border-radius: 4px;
                    }
                }
                @media (max-width: 1280px) {
                    img {
                        outline-width: 4px;
                        border-radius: 4px;
                        outline-offset: -4px;
                    }
                }
                @media (max-width: 1024px) {
                    flex: 0 0 calc(20% - #{math.div(8, 5) * 4}px);
                    margin-right: 8px;
                    img {
                        outline-width: 3px;
                        border-radius: 3px;
                        outline-offset: -3px;
                    }
                }
                @media (max-width: 480px) {
                    padding-top: pc(104 * math.div(5, 4), 846);
                    flex: 0 0 calc(25% - #{math.div(8, 4) * 3}px);
                }
                @media (max-width: 320px) {
                    padding-top: pc(104 * math.div(5, 3), 846);
                    flex: 0 0 calc(33.3333% - #{math.div(8, 3) * 2}px);
                }
            }
            .swiper-slide-thumb-active {
                img {
                    outline-color: rgb(185, 149, 72);
                }
            }
            .spinner {
                width: 30px;
                height: 30px;
                margin-left: -15px;
                margin-top: -15px;
                border-width: 5px;
            }
        }
        .swiper-slide {
            @include fluid-image;
        }
    }
    &__text {
        flex-grow: 1;
        ul {
            display: grid;
            grid-template: auto / repeat(2, 1fr);
            gap: 25px 15px;
            margin: 32px 0 42px;
            word-break: break-word;
            @media (max-width: 1440px) {
                grid-template: auto/auto;
            }
            @media (max-width: 850px) {
                margin-bottom: 0;
            }
            li {
                display: flex;
                align-items: center;
                &::before {
                    flex-shrink: 0;
                    content: "\e901";
                    @extend %ic;
                    color: $mainBlue;
                    border-radius: 50%;
                    border: 2px solid #cecece;
                    font-size: 16px;
                    @include size(62);
                    margin-right: 19px;
                    display: inline-block;
                    @include flex-center;
                    @media (max-width: 400px) {
                        @include size(40);
                        font-size: 12px;
                        border-width: 1px;
                    }
                }
            }
        }
    }
    &__title {
        margin-bottom: 10px;
    }
    &__subtitle {
        font-weight: 400;
        font-size: 24px;
        text-align: center;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    &__text-about-founder {
        display: flex;
        gap: 34px;
        @media (max-width: 480px) {
            flex-direction: column;
            align-items: center;
        }
    }
    .about-founder {
        @media (max-width: 1440px) {
            grid-column: span 2;
        }
        @media (max-width: 850px) {
            grid-column: span 1;
        }
        &__image {
            border-radius: 13px;
        }
        &__image-caption {
            text-align: center;
            font-weight: 400;
            margin-top: 15px;
        }
        &__text {
            margin-bottom: 15px;
        }
        &__text-col {
        }
        &__text-license {
        }
    }
}

.license {
    display: grid;
    grid-template: auto / 74px auto;
    gap: 15px 22px;
    &__image {
        border-radius: 6px;
    }
    &__link {
        text-transform: uppercase;
        text-decoration: 1px dashed underline $mainBlue;
        line-height: math.div(26, 18);
        text-underline-offset: 5px;
    }
    &__notice {
        grid-column: span 2;
        a {
            font-weight: 600;
            color: $mainBlue;
        }
    }
}
