.surgery {
    position: relative;
    @media (max-width: 700px) {
        background-image: none;
        background-color: #fafafa;
    }
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        @media (max-width: 768px) {
            height: 70%;
        }
    }
    // .surgery__container
    &__container {
    }
    // .surgery__title
    &__title {
    }
    [data-tab-bar] {
        display: grid;
        justify-content: center;
        grid-template: auto / auto;
        grid-auto-flow: column;
        gap: 77px;
        margin-bottom: 71px;
        @media (max-width: 1280px) {
            gap: 40px;
        }
        @media (max-width: 992px) {
            grid-template: auto/ repeat(2, auto);
            grid-auto-flow: row;
            margin-bottom: 43px;
        }
        @media (max-width: 550px) {
            grid-template-columns: 1fr;
            gap: 25px;
        }
    }
    [data-tab-button] {
        border-radius: 6px;
        font-size: 20px;
        line-height: math.div(24, 20);
        font-weight: 400;
        padding: 20px 16px;
        text-align: center;
        background: #ffffff;
        box-shadow: 0px 0px 21px 7px rgba(155, 155, 155, 0.13);
        cursor: pointer;
        @include flex-center;
        &::first-letter {
            text-transform: uppercase;
        }
        @media (any-hover: hover) {
            &:hover {
                box-shadow: 0px 0px 21px 7px rgba(155, 155, 155, 0.23);
            }
        }
        @media (max-width: 992px) and (min-width: 551px) {
            &:nth-child(odd) {
                justify-self: self-end;
            }
            &:nth-child(even) {
                justify-self: self-start;
            }
        }
    }
    [data-tab-button-active] {
        background-color: $mainBlue;
        box-shadow: 0px 0px 21px 7px rgba($mainBlue, 0.13);
        color: #fff;
        @media (any-hover: hover) {
            &:hover {
                box-shadow: 0px 0px 21px 7px rgba($mainBlue, 0.23);
            }
        }
    }
    // .surgery__about
    &__about {
        margin-bottom: 54px;
        @media (max-width: 768px) {
            margin-bottom: 30px;
        }
    }
    // .surgery__about-article
    &__about-article {
        & > *:not(:last-child),
        & > div > p:not(:last-child) {
            margin-bottom: 25px;
        }
        @media (max-width: 480px) {
            width: calc(100% + 18px * 2);
            margin-left: -18px;
            margin-right: -18px;
            padding-left: 18px;
            padding-right: 18px;
        }
        ul {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-auto-rows: 1fr;
            column-gap: pc(60, $maxWidthContainer - 114 * 2);
            @media (max-width: 992px) {
                grid-template-columns: auto;
            }
            @media (max-width: 375px) {
                grid-auto-rows: auto;
            }
            li {
                border-bottom: 1px dashed #000;
                display: flex;
                align-items: center;
                font-weight: 400;
                padding: 17px 24px 17px 0;
                &::before {
                    content: "\e906";
                    @extend %ic;
                    font-size: 13px;
                    color: $mainBlue;
                    margin: 0 25px;
                }
                @media (min-width: 993px) {
                    &:nth-child(3n + 1):nth-last-child(-n + 3),
                    &:nth-child(3n + 1):nth-last-child(-n + 3) ~ li {
                        border: none;
                    }
                }
                @media (max-width: 480px) {
                    padding: 10px 15px 10px 0;
                    &::before {
                        font-size: 20px;
                        margin: 0 20px;
                        color: rgba($mainBlue, 0.5);
                    }
                }
            }
        }
    }
    // .surgery__text
    &__text {
        max-width: 80%;
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 83px;
        text-align: center;
        @media (max-width: 1024px) {
            max-width: none;
        }
        @media (max-width: 768px) {
            margin-bottom: 66px;
        }
    }
    .article {
        .tab-text {
            max-width: 1280px;
            margin-left: auto;
            margin-right: auto;
        }
    }

    .rino {
        display: flex;
        overflow: hidden;
        background: #ffffff;
        box-shadow: 0px 13px 40px rgba(0, 0, 0, 0.13);
        border-radius: 40px;
        margin-bottom: 91px;
        @media (max-width: 768px) {
            margin-bottom: 70px;
        }
        @media (max-width: 480px) {
            width: calc(100% + 18px * 2);
            margin-left: -18px;
            margin-right: -18px;
            border-radius: 20px;
        }
        &__text {
            @media (max-width: 660px) {
                margin-bottom: 35px;
            }
        }
        // .rino__image
        &__image {
            position: relative;
            flex: 0 0 pc(340, $maxWidthContainer - $containerPadding * 2);
            padding-top: pc(500, $maxWidthContainer - $containerPadding * 2);
            img {
                position: absolute;
                bottom: 0;
                left: 5%;
                // top: 8%;
                width: 100%;
                object-fit: contain;
                max-height: 92%;
                object-position: left bottom;
                @media (max-width: 1024px) {
                    left: -56px;
                    object-fit: cover;
                }
                @media (max-width: 660px) {
                    position: static;
                    margin-bottom: -10%;
                }
            }
            @media (max-width: 1024px) {
                flex: 0 0 60%;
                left: 0;
                bottom: 0;
                width: 100%;
                margin-bottom: 0;
            }
            @media (max-width: 660px) {
                flex-basis: 100%;
                padding-top: 0;
                max-width: 80%;
                margin: 0 auto 20px;
            }
            @media (max-width: 480px) {
                max-width: none;
                width: calc(100% + 18px * 2);
                margin-left: -18px;
                margin-right: -18px;
            }
        }
        // .rino__content
        &__content {
            padding: 56px 56px 0;
            @media (max-width: 768px) {
                padding: 56px 18px 0;
            }
        }
        // .rino__header
        &__title {
            margin-bottom: 32px;
            @media (min-width: 1025px) {
                text-align: left;
            }
        }
        &__subtitle {
            margin-bottom: 28px;
            @media (max-width: 1024px) {
                text-align: center;
            }
        }
        // .rino__text
        &__text {
            & > *:not(:last-child) {
                margin-bottom: 28px;
            }
            ul {
                li {
                    padding-left: 30px;
                    position: relative;
                    &::before {
                        content: "\e906";
                        @extend %ic;
                        font-size: 13px;
                        position: absolute;
                        margin-left: -30px;
                        color: $mainBlue;
                        @media (min-width: 769px) {
                            top: 50%;
                            transform: translateY(-50%);
                        }
                        @media (max-width: 768px) {
                            margin-top: 6px;
                        }
                    }
                    &:not(:last-child) {
                        margin-bottom: 15px;
                    }
                }
            }
            &-last-part {
                position: relative;
                display: flex;
                @media (max-width: 660px) {
                    flex-direction: column;
                }
            }

            &-last-part-text {
                position: relative;
                & > *:not(:last-child) {
                    margin-bottom: 28px;
                }
                @media (max-width: 1024px) {
                    margin-left: -75px;
                }
                @media (max-width: 660px) {
                    margin-left: 0;
                }
            }
        }
        .more-link {
            color: $mainBlue;
            text-decoration: 2px underline dashed;
            text-underline-offset: 4px;
            font-weight: 600;
            display: inline-block;
            &:not(:last-child) {
                margin-bottom: 46px;
            }
            @media (max-width: 660px) {
                text-align: center;
                display: block;
            }
        }
        &__about-doctor {
            margin-bottom: 42px;
            background: transparent;
            @media (min-width: 1025px) {
                display: none;
            }
            @media (max-width: 660px) {
                margin-bottom: 0;
            }
            .doctor-info__name {
                display: inline-block;
                max-width: 90%;
                @media (max-width: 1024px) {
                    margin-bottom: 15px;
                }
            }
            .doctor-info__position {
                @media (max-width: 375px) {
                    font-size: 14px;
                }
            }
        }
    }
    .surgery {
        // .surgery__services
        &__services {
        }
        // .surgery__services-title
        &__services-title {
            margin-bottom: 25px;
        }
        // .surgery__services-desc
        &__services-desc {
            font-size: 24px;
            line-height: math.div(34.5, 24);
            @media (max-width: 768px) {
                text-align: center;
                font-size: 20px;
            }
        }
    }
    .grid {
        display: grid;
        $columnGap: pc(90, $maxWidthContainer - $containerPadding * 2);

        grid-template: auto / repeat(2, calc(27% - #{math.div($columnGap, 4) * 3})) repeat(
                2,
                calc(22% - #{math.div($columnGap, 4) * 3})
            );
        // gap: 70px 90px;
        column-gap: $columnGap;
        @media (max-width: 1280px) {
            grid-template: auto / repeat(3, 1fr);
            // gap: 50px 70px;
        }
        @media (max-width: 950px) {
            grid-template: auto / repeat(2, 1fr);
        }
        @media (max-width: 1024px) {
            // gap: 20px 50px;
        }
        @media (max-width: 660px) {
            display: flex;
            flex-direction: column;
        }
    }
    .grid-item {
        display: contents;
        grid-template: auto / auto;

        &:nth-child(1) {
        }
        &:nth-child(2) {
            & > * {
                grid-column: 3;
                @media (max-width: 1280px) {
                    grid-column: 1;
                }
            }
            .service__image {
                grid-row: 1;
                @media (max-width: 1280px) {
                    grid-row: 4;
                }
            }
            .service__title {
                grid-row: 2;
                @media (max-width: 1280px) {
                    grid-row: 5;
                }
            }
            .service__desc {
                grid-row: 3;
                @media (max-width: 1280px) {
                    grid-row: 6;
                }
            }
        }
        &:nth-child(3) {
            & > * {
                grid-column: 4;
                @media (max-width: 1280px) {
                    grid-column: 2;
                }
            }
            .service__image {
                grid-row: 1;
                @media (max-width: 1280px) {
                    grid-row: 4;
                }
            }
            .service__title {
                grid-row: 2;
                @media (max-width: 1280px) {
                    grid-row: 5;
                }
            }
            .service__desc {
                grid-row: 3;
                @media (max-width: 1280px) {
                    grid-row: 6;
                }
            }
        }
        &:nth-child(4) {
            & > * {
                grid-column: 1;
                @media (max-width: 1280px) {
                    grid-column: 3;
                }
                @media (max-width: 950px) {
                    grid-column: 1;
                }
            }
            .service__image {
                grid-row: 4;
                @media (max-width: 950px) {
                    grid-row: 7;
                }
            }
            .service__title {
                grid-row: 5;
                @media (max-width: 950px) {
                    grid-row: 8;
                }
            }
            .service__desc {
                grid-row: 6;
                @media (max-width: 950px) {
                    grid-row: 9;
                }
            }
        }
        &:nth-child(5) {
            & > * {
                grid-column: 2;
                @media (max-width: 1280px) {
                    grid-column: 1;
                }
                @media (max-width: 950px) {
                    grid-column: 2;
                }
            }
            .service__image {
                grid-row: 4;
                @media (max-width: 1280px) {
                    grid-row: 7;
                }
            }
            .service__title {
                grid-row: 5;
                @media (max-width: 1280px) {
                    grid-row: 8;
                }
            }
            .service__desc {
                grid-row: 6;
                @media (max-width: 1280px) {
                    grid-row: 9;
                }
            }
        }
        &:nth-child(6) {
            & > * {
                grid-column: 3;
                @media (max-width: 1280px) {
                    grid-column: 2;
                }
                @media (max-width: 950px) {
                    grid-column: 1;
                }
            }
            .service__image {
                grid-row: 4;
                @media (max-width: 1280px) {
                    grid-row: 7;
                }
                @media (max-width: 950px) {
                    grid-row: 10;
                }
            }
            .service__title {
                grid-row: 5;
                @media (max-width: 1280px) {
                    grid-row: 8;
                }
                @media (max-width: 950px) {
                    grid-row: 11;
                }
            }
            .service__desc {
                grid-row: 6;
                @media (max-width: 1280px) {
                    grid-row: 9;
                }
                @media (max-width: 950px) {
                    grid-row: 12;
                }
            }
        }
        &:nth-child(7) {
            & > * {
                grid-column: 4;
                @media (max-width: 1280px) {
                    grid-column: 3;
                }
                @media (max-width: 950px) {
                    grid-column: 2;
                }
            }
            .service__image {
                grid-row: 4;
                @media (max-width: 1280px) {
                    grid-row: 7;
                }
                @media (max-width: 950px) {
                    grid-row: 10;
                }
            }
            .service__title {
                grid-row: 5;
                @media (max-width: 1280px) {
                    grid-row: 8;
                }
                @media (max-width: 950px) {
                    grid-row: 11;
                }
            }

            .service__desc {
                grid-row: 6;
                @media (max-width: 1280px) {
                    grid-row: 9;
                }
                @media (max-width: 950px) {
                    grid-row: 12;
                }
            }
        }
    }
    .grid-item-wide {
        .surgery__services-content {
            grid-row: 1 / span 3;
            grid-column: 1 / span 2;
            @media (max-width: 1280px) {
                grid-column: 1 / span 3;
                text-align: center;
                max-width: 80%;
                margin: 0 auto;
                margin-bottom: 70px;
            }
            @media (max-width: 950px) {
                grid-column: 1 / span 2;
            }
        }
        .surgery__services-title {
            @media (max-width: 1280px) {
                text-align: center;
            }
        }
    }

    .service {
        word-break: break-word;
        @media (max-width: 660px) {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            max-width: 450px;
            margin-left: auto;
            margin-right: auto;
        }
        // .service__image
        &__image {
            @include size(147);
            border-radius: 50%;
            margin-bottom: 15px;
        }
        // .service__title
        &__title {
            font-size: 24px;
            line-height: math.div(29, 24);
            font-weight: 600;
            margin-bottom: 8px;
            @media (max-width: 660px) {
                margin-bottom: 20px;
                max-width: 350px;
            }
        }
        // .service__desc
        &__desc {
            margin-bottom: 70px;
            @media (max-width: 660px) {
                margin-bottom: 40px;
            }
        }
    }
}
