.personal-stories {
    margin-bottom: 60px;
    // .personal-stories__container
    &__container {
        padding: 0 27px;
        @media (max-width: 768px) {
            padding: 0 16px;
        }
    }
    // .personal-stories__slider
    &__slider {
        overflow: hidden;
    }
    .swiper-slide {
        flex: 0 0 calc(25% - #{math.div(30 * 3, 4)}px);
        margin-right: 30px;
        cursor: pointer;
        @media (max-width: 786px) {
            flex: 0 0 calc(50% - #{math.div(30 * 1, 2)}px);
            margin-right: 30px;
        }
        @media (max-width: 480px) {
            flex: 0 0 100%;
        }
    }
    // .personal-stories__controls
    &__controls {
    }
    .popup {
        &-content {
            width: 1280px;
            max-width: 90%;
            margin-left: auto;
            margin-right: auto;
            @media (max-width: 768px) {
                max-width: 100%;
            }
        }
        &-inner {
        }
        &-header {
        }
        &-close-button {
        }
        &-body {
            padding: 0 50px;
            display: flex;
            flex-direction: column;
            @media (max-width: 1280px) {
                padding: 0 30px;
            }
            @media (max-width: 768px) {
                padding: 0 20px;
            }
            .text-content {
                @media (max-width: 1280px) {
                    & > *:not(:last-child) {
                        margin-bottom: 25px;
                    }
                }
                @media (max-width: 768px) {
                    & > *:not(:last-child) {
                        margin-bottom: 20px;
                    }
                }
            }
            .slide__title {
                font-size: 36px;
                margin-bottom: 60px;
                @media (max-width: 1280px) {
                    margin-bottom: 40px;
                    font-size: 28px;
                }
                @media (max-width: 768px) {
                    margin-bottom: 25px;
                    font-size: 24px;
                    max-width: none;
                }
            }
            ul {
                row-gap: 15px;
                margin-left: 30px;
            }
            iframe {
                margin-left: auto;
                margin-right: auto;
                display: block;
                max-width: 100%;
            }
            .media {
                margin-top: 60px;
                @media (max-width: 768px) {
                    margin-top: 30px;
                }
            }
            .images {
                display: flex;
                flex-direction: column;
                gap: 60px;
                img {
                    max-width: 100%;
                    height: auto;
                }
            }
        }
    }
    .slide {
        // .slide__image
        &__image {
            border-radius: 29px;
            overflow: hidden;
            margin-bottom: 20px;
            padding-top: pc(606, 444);
            position: relative;
        }
        // .slide__title
        &__title {
            font-weight: 400;
            text-align: center;
            @media (max-width: 768px) {
                max-width: 80%;
                margin-left: auto;
                margin-right: auto;
            }
        }
    }
}

