.specialization {
    padding-bottom: 30px;
    // .specialization__container
    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    // .specialization__title
    &__title {
    }
    // .specialization__list
    &__list {
        display: grid;
        grid-template-rows: auto;
        grid-template-columns: repeat(6, 1fr);
        margin-bottom: 59px;
        gap: 26px;
        @media (max-width: 1660px) {
            grid-template-columns: repeat(3, 1fr);
            row-gap: 80px;
        }
        @media (max-width: 900px) {
            grid-template-columns: repeat(2, 1fr);
            margin-top: 45px;
        }
        @media (max-width: 600px) {
            grid-template-columns: 1fr;
        }
        @media (max-width: 480px) {
            row-gap: 50px;
        }
        @media (max-width: 480px) {
            width: calc(100% + 10px * 2);
            margin-left: -10px;
            margin-right: -10px;
        }
        @supports (grid-template-rows: subgrid) {
            grid-template-rows: min-content;
        }
    }
    // .specialization__list-item
    &__list-item {
    }
    // .specialication__license-link
    &__license-link {
        color: $mainBlue;
        font-size: 16px;
        line-height: math.div(29, 16);
        font-weight: 600;
        text-align: center;
        display: flex;
        align-items: center;
        text-decoration: 2px underline dashed;
        text-underline-offset: 4px;
    }
}

.spec {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #ffffff;
    box-shadow: 0px 13px 29px rgba(0, 0, 0, 0.09);
    border-radius: 29px;
    padding: 0 20px 42px;
    @media (max-width: 480px) {
        border-radius: 20px;
        padding: 0 15px 30px;
    }
    @supports (grid-template-rows: subgrid) {
        grid-row: span 3;
        row-gap: 0;
    }
    // .spec__number
    &__number {
        font-size: 29px;
        line-height: math.div(43, 29);
        font-weight: 600;
        text-align: center;
        color: #fff;
        @include size(90);
        @include flex-center;
        background: #597fae;
        border-radius: 50%;
        border: 13px solid rgba(#597fae, 0.24);
        margin-top: -45px;
        margin-bottom: 29px;
        background-clip: content-box;
        @media (max-width: 480px) {
            font-size: 22px;
            @include size(60);
            border-width: 8px;
            margin-top: -30px;
            margin-bottom: 20px;
        }
    }
    // .spec__title
    &__title {
        font-size: 24px;
        line-height: math.div(29, 24);
        font-weight: 600;
        margin-bottom: 21px;
        @media (min-width: 1661px) {
            min-height: calc(2em + 1em * .208 * 2);
            display: flex;
            align-items: center;
        }
        @media (max-width: 480px) {
            font-size: 20px;
        }
    }
    // .spec__desc
    &__desc {
        font-weight: 300;
        @media (max-width: 480px) {
            font-size: 16px;
        }
        @supports (grid-template-rows: subgrid) {
            align-self: self-start;
        }
    }
}
@supports (grid-template-rows: subgrid) {
    .spec {
        display: grid;
        grid-template-rows: subgrid;
        justify-items: center;
        align-items: center;
    }
}
