.answers {
    // @include aminvalue("margin-top", -44, -33, 768);
    position: relative;
    --gradient: linear--gradient(#f9f9f9, #fff);
    background-image: url(@img/answers/answers-bg.png), var(--gradient);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    @media (max-width: 1440px) {
        background-image: url(@img/answers/answers-bg-1440.png), var(--gradient);
    }
    @media (max-width: 1280px) {
        background-image: url(@img/answers/answers-bg-1280.png), var(--gradient);
    }
    @media (max-width: 1024px) {
        background-image: url(@img/answers/answers-bg-1024.png), var(--gradient);
    }
    @media (max-width: 768px) {
        background-image: url(@img/answers/answers-bg-768.png), var(--gradient);
    }
    @media (max-width: 480px) {
        background-image: url(@img/answers/answers-bg-480.png), var(--gradient);
    }
    @media (max-width: 320px) {
        background-image: url(@img/answers/answers-bg-320.png), var(--gradient);
    }
    &__container {
        @include aminvalue("padding-top", 44, 33, 768);
        display: grid;
        grid-template: auto/auto 480px;
        column-gap: 31px;
        @media (min-width: 501px) {
            padding-right: 114px - 81px;
        }
        @media (max-width: 1280px) {
            grid-template: auto/auto;
        }
        @media (max-width: 320px) {
            padding-top: 33px;
        }
    }
    &__bg {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        max-height: 100%;
        // otherwise the background moves around
        // as the user open the accordeon items
        // min-height: 2000px;
        z-index: 0;
    }
    &__title {
        grid-column: span 2;
        @media (max-width: 1280px) {
            grid-column: span 1;
        }
    }
    &__answers {
        display: grid;
        align-content: start;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 68px;
        @media (max-width: 1440px) {
            column-gap: 30px;
        }
        @media (max-width: 1280px) {
            // grid-column: span 2;
        }
        @media (max-width: 1024px) {
            grid-template-columns: auto;
            padding-bottom: 0;
        }
        @media (min-width: 1280.02px) {
            padding-bottom: 55px;
        }
    }
    &__answer-list {
        display: flex;
        flex-direction: column;
    }
    &__answer-list-item {
    }
    .request {
        margin-top: 80px;
        @media (max-width: 1280px) {
            margin-bottom: 50px;
        }
        @media (max-width: 1024px) {
            margin-bottom: 30px;
        }
        @media (max-width: 480px) {
            margin-top: 50px;
        }
        &__body {
            display: flex;
            align-items: center;
            gap: 20px;
            color: $mainBlue;
            font-weight: 600;
            @media (max-width: 1900px) {
                flex-direction: column;
                text-align: center;
                gap: 30px;
            }
        }
        &__text {
        }
        &__button {
            font-size: 16px;
            line-height: math.div(29, 16);
            white-space: nowrap;
            @media (max-width: 400px) {
                padding: 20px;
                width: 100%;
            }
        }
    }
    &__doctor {
        display: flex;
        flex-direction: column-reverse;
        position: relative;
        @media (max-width: 1280px) {
            justify-content: end;
            flex-direction: row-reverse;
        }
        @media (max-width: 700px) {
            flex-direction: column-reverse;
            align-items: center;
            row-gap: 30px;
        }
    }
    &__doctor-image {
        max-width: 100%;
        height: auto;
    }
    &__doctor-text {
        text-align: right;
        position: absolute;
        top: 128px;
        left: -10px;
        max-width: 180px;
        .em {
            font-size: 24px;
        }
        @media (max-width: 1280px) {
            position: static;
            max-width: 320px;
            text-align: center;
        }
        @media (max-width: 1024px) {
            margin-right: -66px;
        }
        @media (max-width: 700px) {
            margin-right: 0;
        }
    }
    .answer {
        border-bottom: 2px dashed #c3cbd9;
        &__header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 22px 0 20px;
            // margin-bottom: 15px;
            cursor: pointer;
            &::before {
                transition: transform 0.3s;
                content: "\e906";
                @extend %ic;
                color: $mainBlue;
                font-size: 13px;
                margin-right: 30px;
                display: block;
            }
            @media (any-hover: hover) {
                &:hover {
                    .answer__title {
                        color: #597fae;
                    }
                    .answer__counter {
                        color: #597fae;
                    }
                }
            }
        }
        &__title {
            font-weight: 600;
            flex-grow: 1;
            transition: color .3s;
        }
        &__counter {
            @include size(48);
            margin-left: .8em;
            flex-shrink: 0;
            @include flex-center;
            text-align: center;
            border-radius: 50%;
            font-weight: 600;
            color: #cac8c8;
            background-color: #f7f7f7;
            transition: color 0.6s, background-color .6s;
            @media (max-width: 400px) {
                margin-left: 30px;
                display: none;
            }
        }
        &__body {
            font-size: 16px;
            line-height: math.div(22, 16);
            margin-right: 57px;
            margin-left: 44px;
            padding-bottom: 20px;
            @media (max-width: 500px) {
                margin: 0;
            }
        }
        &__images {
            display: flex;
            flex-wrap: wrap;
            gap: 20px;
            margin: 20px 0;
        }
        ._spoiler-open {
            .answer__counter {
                background-color: $mainBlue;
                color: #fff;
            }
            &.answer__header {
                &::before {
                    transform: rotate(90deg);
                }
                @media (any-hover: hover) {
                    &:hover {
                        .answer__counter {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
}
