.intro {
    position: relative;
    overflow: hidden;
    border-radius: 0 0 90px 90px;
    @media (max-width: 1440px) {
        border-radius: 0 0 50px 50px;
    }
    @media (max-width: 1368px) {
        border-radius: 0 0 40px 40px;
    }
    @media (max-width: 1280px) {
        border-radius: 0 0 40px 40px;
    }
    @media (max-width: 700px) {
        background-size: cover;
    }
    @media (max-width: 480px) {
        border-radius: 0;
    }
    @media (max-width: 440px) {
    }
    @media (max-width: 375px) {
    }
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        opacity: 0;
        transition: opacity .3s;
        &.loaded {
            opacity: 1;
        }
    }
    // .intro__container
    &__container {
        position: relative;
        display: flex;
        @media (min-width: 1701px) {
            padding-left: 113px;
            padding-right: 113px;
        }
        @media (max-width: 1280px) {
            flex-direction: column;
        }

    }
    &__main-content {
        padding-bottom: 30px;
    }
    // .intro__offer
    &__offer {
        @include aminvalue("font-size", 69, 46, 768);
        @include amaxvalue("font-size", 46, 36, 767);
        margin-top: 82px;
        line-height: math.div(95, 69);
        font-weight: 300;
        &:not(:last-child) {
            margin-bottom: 40px;
        }
        em {
            font-weight: 700;
        }
        @media (max-width: 1280px) {
            margin-top: 53px;
            text-align: center;
            &:not(:last-child) {
                margin-bottom: 25px;
            }
        }
        @media (max-width: 700px) {
            margin-top: 0;
        }
    }
    // .intro__promo
    &__promo {
        display: flex;
        gap: 30px;
        &:not(:last-child) {
            margin-bottom: 40px;
        }
        @media (max-width: 1280px) {
            flex-direction: column;
        }
    }
    // .intro__promo-col
    &__promo-col {
        @media (max-width: 1280px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    // .intro__promo-title
    &__promo-title {
        font-size: 31px;
        @include amaxvalue("font-size", 31, 20, 767);
        line-height: math.div(38, 31);
        font-weight: 300;
        em {
            font-weight: 600;
        }
        &:not(:last-child) {
            margin-bottom: 40px;
        }
        @media (max-width: 1280px) {
            text-align: center;
            margin-top: 20px;
        }
    }
    // .intro__promo-service-list
    &__promo-service-list {
        font-weight: 300;
        display: grid;
        grid-template: auto / repeat(2, 1fr);
        gap: 30px;
        em {
            font-weight: 600;
        }
        @media (max-width: 768px) {
            grid-template: auto / auto;
        }
    }
    // .intro__promo-service-item
    &__promo-service-item {
        display: flex;
        align-items: center;
        gap: 23px;
        &::before {
            content: "\e901";
            @extend %ic;
            width: 66px;
            height: 66px;
            flex-shrink: 0;
            border-radius: 50%;
            border: 1.5px solid #cecece;
            color: $mainBlue;
            font-size: 17px;
            @include flex-center;
        }
        @media (max-width: 767px) {
        }
    }

    &__thumbnail-col {
        padding: 30px 90px;
        @include flex-center;
        flex-direction: column;
        gap: 10px;
    }
    // .intro__promo-video-thumbnail
    &__promo-video-thumbnail {
        border-radius: 50%;
        @include flex-center;
        width: 144px;
        height: 144px;
        border-radius: 50%;
        background-color: rgba($mainBlue, 0.2);
        position: relative;
        cursor: pointer;
        &:after {
            @extend %ic;
            content: "\e904";
            color: #fff;
            @include abs-center;
            z-index: 10;
        }
        img {
            width: 120px;
            height: 120px;
            border-radius: 50%;
            border: 12px solid rgba($mainBlue, 0.4);
        }
    }
    // .intro__promo-video-title
    &__promo-video-title {
        text-align: center;
        font-weight: 300;
        em {
            font-weight: 600;
        }
    }
    // .intro__actions
    &__actions {
        display: flex;
        gap: 30px;
        @media (max-width: 1280px) {
            flex-direction: column;
            align-items: center;
        }
    }
    // .intro__actions-col
    &__actions-col {
        @media (max-width: 480px) {
            width: 100%;
        }
    }
    &__register {
    }
    &__contacts {
    }
    &__actions-and-video {
        display: none;
        @media (max-width: 1700px) {
            display: flex;
            // justify-content: space-between;
            margin-top: 53px;
            position: relative;
            z-index: 5;
            gap: 100px;
            align-items: flex-start;
        }
        @media (max-width: 1280px) {
            gap: 20px;
            align-items: center;
            justify-content: space-between;
        }
        & > div {
            flex: 0 0 200px;
            padding: 0;
        }
        @media (max-width: 700px) {
            justify-content: space-around;
        }
        @media (max-width: 480px) {
            flex-direction: column;
            margin-top: 40px;
            & > div {
                flex: auto;
            }
        }
    }
    &__actions-button {
        @media (max-width: 480px) {
            padding: 15px 10px;
            border-radius: 20px;
            width: 100%;
            display: block;
            span {
                padding: 0;
            }
            &::after {
                display: none;
            }
        }
        &:not(:last-child) {
            margin-bottom: 14px;
        }
    }
    &__actions-note {
        text-align: center;
        font-weight: 300;
    }
    &__actions-asterisk {
        color: $mainBlue;
    }
    // .intro__actions-hotline
    &__actions-hotline {
        font-weight: 300;
        margin-bottom: 11px;
        display: flex;
        &::before {
            content: "";
            width: 11px;
            height: 11px;
            margin-top: 6px;
            margin-left: 0px;
            background-color: #00b327;
            border-radius: 50%;
            display: block;
            margin-right: 7px;
        }
        em {
            font-weight: 600;
        }
        @media (max-width: 480px) {
            text-align: center;
            display: block;
            &::before {
                display: none;
            }
        }
    }
    // .intro__actions-contact-list
    &__actions-contact-list {
        margin-left: 15px;
        @media (max-width: 480px) {
            margin: 0;
            justify-content: center;
        }
    }
    // .intro__actions-contact-item
    &__actions-contact-item {
    }
    // .intro__doctor
    &__doctor {
        position: relative;
        bottom: 0;
        flex: 0 0 614px;
        right: 79px;
        z-index: 0;
        max-height: 100%;
        display: flex;
        align-items: flex-end;
        @media (max-width: 1900px) {
            right: 0;
        }
        @media (max-width: 1280px) {
            flex: 0 0 525px;
            max-height: 525px;
            height: 525px;
            overflow: hidden;
            display: flex;
            justify-content: center;
            margin-top: -280px;
            right: auto;
            align-items: stretch;
            img {
                height: 100%;
                width: auto;
            }
        }
        @media (max-width: 700px) {
            margin-top: -80px;
            width: calc(100% + 18px * 2);
            margin-left: -18px;
        }
    }
    // .intro__doctor-photo
    &__doctor-photo {
        @media (min-width: 1280.02px) {
            width: 614px;
            height: 100%;
        }
    }
    // .intro__doctor-phone-about
    &__doctor-photo-about {
        position: absolute;
        bottom: 54px;
        transform: translateX(-30%);
        background: transparent;
        @media (max-width: 1900px) {
            left: 50%;
            transform: translateX(-50%);
        }
        @media (max-width: 1700px) {
            left: 50%;
            transform: translateX(-50%);
        }
        @media (max-width: 1280px) {
            transform: translateX(-50%);
            bottom: 30px;
        }
        @media (max-width: 700px) {
            transform: translateX(-50%);
            width: 80%;
        }
        @media (max-width: 425px) {
        }
    }
    // .intro__doctor-name
    &__doctor-name {
        &:not(:last-child) {
            margin-bottom: 14px;
        }
        @media (max-width: 480px) {
            width: 100%;
        }
    }
    // .intro__doctor-position
    &__doctor-position {
    }
}
