.certifications {
    @include aminvalue("padding-top", 90, 45, 768);
    @include aminvalue("padding-bottom", 90, 45, 768);
    background-color: #f7f7f7;
    // .certifications__container
    &__container {
        @media (max-width: 500px) {
            padding: 0;
        }
    }
    // .certificationsbody
    &__body {
        background: #ffffff;
        border-radius: 40px;
        display: flex;
        gap: 50px;
        padding: 60px 60px 40px 90px;
        @media (max-width: 1440px) {
            padding: 60px 40px;
        }
        @media (max-width: 1200px) {
            display: flex;
            padding: 44px 22px;
            flex-direction: column;
            gap: 0;
        }
        @media (max-width: 500px) {
            padding: 30px 15px;
            border-radius: 0;
        }
    }
    // .certifications__text
    &__text {
        flex: 0 0 pc(378, 1920 - 114 * 2);
        & > * {
            margin-bottom: 25px;
        }
        @media (max-width: 1200px) {
            text-align: center;
        }
    }
    // .certifications__title
    &__title {
        line-height: math.div(61, 54);
        text-align: left;
        margin-bottom: 20px;
        @media (max-width: 1200px) {
            text-align: center;
        }
    }
    // .certifications__slider
    &__slider {
        min-width: 0;
        flex: 1;
    }
    .swiper-slide {
        border-radius: 13px;
        flex: 0 0 calc(33.333% - #{math.div(23 * 2, 3)}px);
        margin-right: 23px;
        overflow: hidden;
        position: relative;
        padding-top: pc(354, 1920 - 114 * 2);
        @media (max-width: 1440px) {
            flex: 0 0 calc(50% - #{math.div(23 * 1, 2)}px);
            padding-top: pc(234, 337 * 2 + 23);
        }
        @media (max-width: 767px) {
            padding-top: pc(247, 356);
            flex: 0 0 100%;
            margin-right: 0;
        }
    }
    &-controls {
        margin-top: 33px;
    }
}
