.privacy-policy {
    @include avalue("margin-top", 80, 30);
    &__container {
        @media (max-width: 768px) {
            padding: 0;
        }
    }
    .text-content {
        padding: 80px 40px;
        border-radius: 40px;
        background-color: #fff;
        word-wrap: break-word;
        @media (max-width: 768px) {
            padding: 30px 15px;
            border-radius: 30px;
        }
        @media (max-width: 480px) {
            font-size: 16px;
            border-radius: 10px;
        }
        h1 {
            font-weight: 600;
        }
        h2 {
            margin-top: 50px;
            font-weight: 600;
            font-size: 1.5em;
            text-transform: uppercase;
            @media (max-width: 768px) {
                margin-top: 30px;
            }
            @media (max-width: 480px) {
                font-size: 1.25em;
                font-weight: 500;
            }
        }
        ul {
            li {
                &::before {
                    display: none;
                }
            }
        }
    }
}
