.map {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f7f7f7;
    @media (max-width: 1000px) {
        align-items: stretch;
    }
    &__map {
        width: 100%;
        height: 600px;
        background-color: rgba(#597fae, 0.1);
        position: relative;
        @media (max-width: 1440px) {
            height: 500px;
        }
        @media (max-width: 1024px) {
            height: 400px;
        }
        iframe {
            width: 100%;
            height: 100%;
            position: relative;
            z-index: 5;
        }
    }
    &__card {
        display: flex;
        background-color: #fff;
        border-radius: 40px;
        margin: 25px auto;
        justify-content: center;
        column-gap: 63px;
        padding: 65px;
        @media (max-width: 1440px) {
            left: 50%;
            padding: 46px;
            column-gap: 40px;
        }
        @media (max-width: 1000px) {
            border-radius: 0;
            margin: 0;
        }
        @media (max-width: 700px) {
            transform: none;
            padding: 30px 15px;
            flex-direction: column;
            align-items: center;
            row-gap: 30px;
        }
    }
    &__contacts {
        @media (max-width: 700px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    &__contacts-phone {
        margin-bottom: 24px;
        @media (max-width: 700px) {
            text-align: center;
        }
    }
    &__contacts-socials-block {
        display: flex;
        gap: 40px;
        @media (max-width: 700px) {
            justify-content: space-around;
            text-align: center;
            gap: 20px;
        }
    }
    &__contacts-phone-text {
    }
    &__contacts-phone-link {
        @media (max-width: 700px) {
            display: inline-flex;
        }
    }
    &__contacts-address {
        margin-bottom: 24px;
        display: flex;
        align-items: center;
        column-gap: 13px;
        @media (max-width: 700px) {
            text-align: center;
        }
    }
    .icon-map {
        &::before {
            font-size: 26px;
            color: $mainBlue;
        }
    }
    &__contacts-socials {
    }
    &__contacts-socials-text {
    }
    &__social-list {
        @media (max-width: 700px) {
            justify-content: center;
        }
    }
    &__social-list-item {
        &.contact-list__item {
            svg {
                @include size(47);
            }
        }
    }
    &__gallery {
        @media (max-width: 700px) {
            text-align: center;
        }
    }
    &__gallery-title {
        margin-bottom: 18px;
    }
    &__gallery-images {
        display: grid;
        grid-template: auto / repeat(2, auto);
        gap: 15px;
        @media (max-width: 1024px) {
            gap: 12px;
        }
        @media (max-width: 700px) and (min-width: #{142 * 4 + 12 * 3 + 15* 2 + 1}px) {
            grid-template: auto / repeat(4, auto);
        }
        @media (max-width: 360px) {
            grid-template: auto/auto;
        }
    }
    &__gallery-image-list-item {
    }
    &__gallery-image-link {
    }
    &__gallery-image {
        border-radius: 13px;
        width: 142px;
        height: 104px;
        object-fit: cover;
        @media (max-width: 1024px) {
            border-radius: 6px;
        }
    }
}
