.footer {
    &__container {
        display: flex;
        padding: 20px;
        justify-content: space-between;
        align-items: center;
        gap: 40px;
        @media (max-width: 1024px) {
            display: grid;
            grid-template: auto / repeat(2, auto);
            align-items: start;
        line-height: 1.5;

        }
        @media (max-width: 599px) {
            grid-template: auto / auto;
            justify-items: center;
            gap: 25px;
            padding: 20px 15px;
        }
    }
    &__logo {
        @media (max-width: 1024px) and (min-width: 600px) {
            grid-row: span 3;
        }
    }
    &__contacts {
        @media (max-width: 1024px) {
            text-align: center;
        }
    }
    &__copyright {
        text-align: center;
    }
    &__privacy {
        font-size: 16px;
        line-height: math.div(29, 16);
        font-weight: 600;
        text-decoration: 1px dashed underline;
        text-underline-offset: 4px;
        color: $mainBlue;
        text-align: center;
    }
}
