.header {
    position: relative;
    z-index: 10;
    em {
        font-weight: 500;
    }

    .upper-header {
        width: 100%;
        @media (max-width: 992px) {
            background-color: #F0F0F3;
            position: fixed;
            top: 0;
        }
        // .upper-header__container
        &__container {
            min-height: 197px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
            width: 100%;
            @media (max-width: 992px) {
                min-height: auto;
                padding-top: 15px;
                padding-bottom: 15px;
                gap: 20px;
            }
            @media (max-width: 420px) {
                padding-top: 10px;
                padding-bottom: 10px;
                gap: 15px;
            }
        }
        &__main-block {
            display: flex;
            gap: 30px;
            @media (max-width: 992px) {
                gap: 15px;
            }
        }
        &__contacts-block {
            display: flex;
            gap: 30px;
            justify-content: space-between;
            @media (max-width: 900px) {
               width: 100%; 
               align-items: center;
               gap: 15px;
            }
            @media (max-width: 420px) {
                justify-content: flex-end;
            }
        }
        // .upper-header__logo
        &__logo {
            img {
                height: auto;
            }
            @media (max-width: 992px) {
                img {
                    max-width: 100px;
                }
            }
            @media (max-width: 480px) {
                img {
                    max-width: 80px;
                }
            }
        }
        &__info-block {
            max-width: 30%;
            @media (max-width: 992px) {
                max-width: 100%;
            }
            @media (max-width: 900px) {
                display: none;
            }
        }
        // .upper-header__about-doctor
        &__about-doctor {
            @media (max-width: 480px) {
                display: none;
            }
        }
        // .upper-header__about-clinic
        &__about-clinic {
            @media (max-width: 1700px) {
                display: none;
            }
        }
        // .upper-header__clinic-rating
        &__clinic-rating {
            @media (max-width: 1700px) and (min-width: 769px) {
                display: none;
            }
        }
        // .upper-header__social-list
        &__social-list {
            display: flex;
            gap: 22px;
            @media (max-width: 992px) {
                display: none;
            }
        }
        // .upper-header__social-list-item
        &__social-list-item {
        }
        // .upper-header__contacts
        &__contacts {
            text-align: right;
            @media (max-width: 992px) {
                font-size: 14px;
                text-align: left;
            }
            @media (max-width: 420px) {
                display: none;
            }
        }
        // .upper-header__phone
        &__phone {
            font-size: 30px;
            // line-height: math.div(21, 30);
            color: #000;
            white-space: nowrap;
            font-weight: 900;
            display: block;
            &:not(:last-child) {
                // margin-bottom: 10px;
            }
            @media (max-width: 992px) {
                font-size: 24px;
            }
            @media (max-width: 600px) {
                font-size: 20px;
            }
            @media (max-width: 440px) {
                font-size: 16px;
            }
        }
        // .upper-header__address
        &__address {
            text-decoration: 1px underline dashed $mainBlue;
            white-space: nowrap;
            text-underline-position: under;
        }
        // .upper-header__burger
        &__burger {
        }
    }
}
.info-block {
    display: flex;
    column-gap: 20px;
    align-items: center;

    // .info-block__figure
    &__figure {
        width: 95px;
        height: 95px;
        flex: 0 0 95px;
        @include flex-center;
        img {
            border-radius: 50%;
            box-shadow: 0px 0px 40px rgba(19, 19, 19, 0.11);
        }
    }
    // .info-block__text
    &__text {
    }
    // .info-block__rating
    &__rating {
        @include flex-center;
        color: $mainBlue;
        background-color: #fff;
        border-radius: 50%;
        font-size: 24px;
        font-weight: 900;
        text-align: center;
        flex: 0 0 84px;
        height: 84px;
        box-shadow: 0px 0px 40px rgba(19, 19, 19, 0.11);
    }
    &__stars {
        display: flex;
        gap: 7px;
        margin-bottom: 10px;
        svg {
            width: 22px;
            height: 21px;
        }
    }
    a {
        text-decoration: 1px underline dashed;
        text-underline-position: under;
        color: $mainBlue;
    }
}

.lower-header {
    transition: background-color .3s;
    &._sticky {
        @media (min-width: 992px) {
            position: fixed;
            top: 0;
            background-color: #fff;
            width: 100%;
            box-shadow: 0 5px 5px rgba(19, 19, 19, 0.26);
            .lower-header__menu-list {
                box-shadow: none;
            }
        }
    }
    @media (max-width: 992px) {
        margin-top: 197px;
    }
    @media (max-width: 992px) {
        margin-top: 160px;
    }
    @media (max-width: 480px) {
        margin-top: 140px;
    }
    @media (max-width: 420px) {
        margin-top: 110px;
    }
    // .lower-header__container
    &__container {
    }
    // .lower-header__menu
    &__menu {
        @media (max-width: 992px) {
            display: none;
        }
    }
    // .lower-header__menu-list
    &__menu-list {
        box-shadow: 0px 0px 40px rgba(19, 19, 19, 0.06);
        border-radius: 11px;
        display: flex;
        flex-wrap: wrap;
        text-align: center;
        color: #000;
        align-items: center;
        justify-content: space-around;
        background-color: #fff;
        text-transform: uppercase;
        font-weight: 500;
        @include aminvalue('font-size', 18, 11, 993);
        line-height: math.div(24, 18);
        li {
            a {
                padding: 1.7em .5em; 
                display: inline-block;
                transition: color .3s;
                @media (any-hover: hover) {
                    &:hover {
                        color: #597fae;
                    }
                }
            }
        }
    }
    &__rating-and-contacts {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 30px;
        @media (max-width: 700px) {
            display: none;
            flex-direction: column;
            justify-content: space-around;
            * > div {
                max-width: 350px;
            }
        }
    }
}
.icon-menu {
    display: none;
    @media (max-width: 992.02px) {
        display: block;
        position: relative;
        width: 85px;
        height: 52px;
        flex: 0 0 85px;
        cursor: pointer;
        z-index: 5;
        @media (max-width: 500px) {
            flex-basis: 60px;
        }
        @media (max-width: 420px) {
            height: 40px;   
        }
        span,
        &::before,
        &::after {
            content: "";
            @include transition;
            position: absolute;
            left: 0;
            width: 100%;
            height: 7px;
            border-radius: 10px;
            background-color: $mainBlue;
            @media (max-width: 420px) {
                height: 5px;
            }
        }
        &::before {
            top: 0;
        }
        &::after {
            bottom: 0;
        }
        span {
            top: calc(50% - 3px);
        }
        .menu-open & {
            span {
                transform: scale(0);
            }
            &::before {
                transform: rotate(-45deg);
                top: calc(50% - 1px);
            }
            &::before {
                transform: rotate(45deg);
                top: calc(50% - 1px);
            }
        }
    }
}
.burger-popup {
    .popup {
        opacity: 1;
        z-index: 130;
        padding: 0;
        transition: transform .3s;
        transform: translateX(100%);
        background-color: transparent;
        justify-content: flex-end;
        &-visible {
            transform: translateX(0);
        }
        &-inner {
            width: 100%;
            border-radius: 0;
            padding: 53px 0 0 0;
        }
        &-body {
            padding-bottom: 0;
            border-style: solid;
            border-width: 1px 0;
            border-color: #ddd;
        }
        &-content {
            width: 100%;
            max-width: 400px;
        }
        .lower-header__menu {
            display: block;
        }
        .lower-header__menu-list {
            gap: 0;
            font-weight: 400;
            flex-direction: column;
            width: 100%;
            border-radius: 0;
            padding: 0;
            li {
                width: 100%;
                &:not(:last-child) {
                    border-bottom: 1px solid #ddd;
                }
                a {
                    display: block;
                    padding: 30px;
                }
            }
        }
    }
}
