button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    color: inherit;
}
input,
textarea {
    font-weight: inherit;
    background-color: inherit;
    font-size: inherit;
    line-height: inherit;
    font-weight: inherit;
    &::placeholder {
        font-size: inherit;
    }
    &:-webkit-autofill,
    &:-webkit-autofill:focus {
        transition: background-color 600000s 0s, color 600000s 0s;
    }
}
textarea {
    display: block;
}
a {
    line-height: inherit;
    font-size: inherit;
    color: inherit;
}
th {
    text-align: left;
    font-weight: inherit;
}
address {
    font-style: normal;
}
[data-spoiler] {
    cursor: pointer;
}
.svg-sprite {
    height: 0;
}
.contacts-phone {
    &-text {
        margin-bottom: 15px;
        position: relative;
        &::before {
            content: "";
            @include size(10);
            border-radius: 50%;
            display: inline-block;
            margin-right: 10px;
            background-color: #00b327;
        }
    }
    &-link {
        font-weight: 700;
        font-size: 31px;
        line-height: 1;
        display: flex;
        align-items: center;
        column-gap: 10px;
        &::before {
            color: $mainBlue;
            font-size: 24px;
        }
        @media (max-width: 480px) {
            font-size: 24px;
            &::before {
                font-size: 18px;
            }
        }
    }
}
.contacts-socials {
    &-text {
        margin-bottom: 11px;
    }
    &-list {
        display: flex;
        gap: 12px;
        svg, img {
            width: 47px;
            height: 47px;
        }
    }
}
.doctor-info {
    text-align: center;
    &__name {
        padding: 20px 40px;
        color: #fff;
        background: #52ae95;
        box-shadow: 0px 0px 40px rgba(123, 123, 123, 0.26);
        border-radius: 13px;
        font-weight: 600;
        font-size: 20px;
        line-height: math.div(24, 20);
        transform: skewX(-10deg);
        display: inline-block;
        margin-bottom: -15px;
        position: relative;
        z-index: 10;
        text-align: center;
        span {
            transform: skewX(10deg);
            display: block;
        }
        @media (max-width: 500px) {
            width: 100%;
            padding: 15px 30px;
        }
        @media (max-width: 375px) {
            padding: 15px 15px;
        }
    }
    &__position {
        font-weight: 400;
    }
    &__quote {
        background: #ffffff;
        box-shadow: 0px 0px 40px rgba(123, 123, 123, 0.26);
        border-radius: 13px;
        transform: skewX(-10deg);
        padding: 30px 36px 25px;
        & > span {
            transform: skewX(10deg);
            display: block;
            text-align: center;
        }
        @media (max-width: 500px) {
            padding: 25px 20px 20px;
            transform: none;
            & > span {
                transform: none;
            }
        }
    }
}
.more-link {
    color: $mainBlue;
    text-decoration: 2px underline dashed;
    text-underline-offset: 6px;
}
[data-tabs-container] {
    [data-tab-button] {
    }
    [data-tab-bar] {
        [data-tab-button] {
        }
        [data-tab-button-active] {
            // opacity: 1;
        }
    }
    [data-tab] {
        display: none;
        // opacity: 0;
        transition: all 0.3s;
    }

    [data-tab-active] {
        display: block;
        // opacity: 1;
    }
    .tab-is-fading-out {
        // opacity: 0;
        display: block;
    }
    .tab-is-fading-in {
        display: block;
        // opacity: 1;
    }
}
[class*="__slider"] {
    overflow: hidden;
}
.slider-controls {
    // .comparison__controls

    @include flex-center;
    margin-top: 52px;
    position: relative;
    @media (max-width: 480px) {
        display: grid;
        grid-template: auto / repeat(2, 1fr);
        justify-items: center;
        margin-top: 0;
    }
    &-pagination {
        display: flex;
        gap: 27px;
        @media (max-width: 480px) {
            grid-area: 1 / 1 / 2 / 4;
            padding: 20px 0;
        }
        .swiper-pagination-bullet {
            @include size(13);
            border-radius: 13px;
            background-color: #c4c4c4;
            cursor: pointer;
            transition: background-color .3s, transform .3s;
            &-active {
                background-color: #597fae;
                transform: scale(1.5);
            }
        }
    }
    &-prev-button,
    &-next-button {
        border-radius: 50%;
        color: #cecece;
        border: 2px solid;
        @include flex-center;
        @include size(73);
        flex-shrink: 0;
        cursor: pointer;
        transition: color .3s, background-color .3s, opacity .3s;
        &::before {
            content: "\e905";
            @extend %ic;
            font-size: 17px;
        }
        &:not(.swiper-button-disabled) {
            @media (any-hover: hover) {
                &:hover {
                    color: #597fae;
                    background-color: #597fae;
                    &::before {
                        color: #fff;
                    }
                }
            }
        }
        &.swiper-button-disabled {
            opacity: 0.25;
            cursor: not-allowed;
        }
    }
    // .comparison__controls-prev-button
    &-prev-button {
        margin-right: 53px;
        &::before {
            transform: rotate(180deg);
        }
        @media (max-width: 480px) {
            margin-right: 0;
        }
    }
    // .comparison__controls-next-button
    &-next-button {
        margin-left: 53px;
        @media (max-width: 480px) {
            margin-left: 0;
        }
    }
}
.text-content {
    & > *:not(:last-child) {
        margin-bottom: 30px;
        @media (max-width: 480px) {
            margin-bottom: 15px;
        }
    }
    ul {
        display: flex;
        flex-direction: column;
        row-gap: 23px;
        li {
            padding-left: 34px;
            position: relative;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                background-clip: content-box;
                left: 0;
                transform: translateY(-50%);
                @include size(21);
                background-color: $mainBlue;
                border: 5px solid rgba($mainBlue, 0.2);
                border-radius: 50%;
            }
        }
    }
    iframe {
        background-color: rgba(#597fae, .1);
        position: relative;
        z-index: 5;
    }
    &-video {
        position: relative;
        .spinner {
            @include size(120);
            z-index: 0;
        }
    }
}
body img.spinner {
    @include size(80);
    @include abs-center;
    z-index: 0;
 }
.contact-list {
    display: flex;
    gap: 19px;
    flex-direction: row !important;
    .contact-list__item {
        padding: 0;
        &:before,
        &:after {
            display: none;
        }
        svg {
            @include size(74);
            border-radius: 50%;
            color: transparent;
            transition: color .3s;
            box-shadow: 1px 1px 7px;
        }
        @media (any-hover: hover) {
            &:hover {
                ._icon-telegram {
                    color: #37AEE2;
                }
                ._icon-vk {
                        color: #4D76A1;
                }
                ._icon-whatsapp {
                        color: #56D263;
                }
            }
        }
    }
}
.dark-button {
    border-radius: 100px;
    background-color: $mainBlue;
    font-size: 19px;
    line-height: math.div(29, 19);
    font-weight: 600;
    text-align: center;
    @include flex-center;
    gap: 20px;
    padding: 25px 50px;
    color: #fff;
    span {
        padding: 0 15px;
    }
    &:after {
        content: "\e900";
        @extend %ic;
    }
}
.section-title {
    @include aminvalue("font-size", 54, 31, 768);
    @include amaxvalue("font-size", 31, 24, 767);
    line-height: math.div(75, 54);
    font-weight: 300;
    text-align: center;
    margin-bottom: 70px;
    .em,
    em {
        font-weight: 700;
    }
    @media (max-width: 768px) {
        line-height: math.div(38, 31);
        margin-bottom: 25px;
    }
}
.section-title-smaller {
    text-align: left;
    @include aminvalue("font-size", 54, 31, 768);
    line-height: math.div(70, 54);
    font-weight: 300;
    .em,
    em {
        font-weight: 700;
    }
    @media (max-width: 768px) {
        text-align: center;
    }
}
[class*="__title-smaller"] {
    @include avalue("font-size", 26, 20);
    line-height: 1.4;
    font-weight: 600;
}
[class*="__input"] {
    transition: border-color 0.15s;
    &[aria-invalid="true"] {
        border-color: red;
    }
}

::selection {
    background-color: $mainBlue;
    color: #fff;
}
.em,
em {
    font-weight: 600;
    @media (max-width: 480px) {
        font-weight: 500;
    }
}
.swiper-wrapper {
    display: flex;
    position: relative;
}
.swiper-pagination-bullets-dynamic {
    display: block !important;
    white-space: nowrap;
    text-align: center;
    overflow: hidden;
    transition: all 0.3s;
    position: relative;
    .swiper-pagination-bullet {
        transition: all 0.3s;
        display: inline-block;
        position: relative;
        margin: 0 13px;
    }
}
.swiper-slide {
}
._adaptive-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        opacity: 1;
        transition: opacity .3s;
    }
}
._bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}
._lazy-image {
    background-color: rgba(#597fae, .1);
    img {
        opacity: 1;
        transition: opacity .3s;
    }
    img.loading, img.error {
        visibility: hidden;
        opacity: 0;
    }
}
.swiper-lazy-preloader, .spinner {
    width: 80px;
    height: 80px;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -40px;
    margin-top: -40px;
    z-index: -1;
    transform-origin: 50%;
    box-sizing: border-box;
    border: 10px solid rgba(#597fae, .1);
    border-radius: 50%;
    border-top-color: transparent;
    animation: swiper-preloader-spin 1s infinite linear;
}
@keyframes swiper-preloader-spin {
    0% {
        transform: rotate(0deg)
    }

    100% {
        transform: rotate(360deg)
    }
}
._error-list {
    margin-top: rem(5);
    color: red;
    font-size: rem(12);
    min-height: rem(17 * 2);
    opacity: 0;
    transition: opacity 0.3s linear;
    &-visible {
        opacity: 1;
    }
    li:not(:last-child) {
        margin-bottom: rem(7);
    }
}
.special-offer {
    position: relative;
    &::after {
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: url(@img/special-offer.svg) no-repeat;
        background-size: 100% 100%;
        z-index: -1;
    }
    &__text {
        font-size: rem(20);
        line-height: math.div(28, 20);
        font-weight: 600;
        position: absolute;
        left: 0;
        top: 0;
        text-align: center;
        transform: translate(39%, 160%);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
        // display: none;
    }
}

.header-burger-button {
    opacity: 0.8;
    font-weight: 600;
    display: flex;
    align-items: center;
    font-size: inherit;
    line-height: inherit;
    height: 24px;
    white-space: nowrap;
    .burger-lines {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 34px;
        height: 21px;
        span {
            height: 1px;
            opacity: 1;
            width: 34px;
            background-color: #fff;
            &:nth-child(2) {
                width: 26px;
            }
            &:nth-child(3) {
                width: 30px;
            }
        }
    }
    .burger-text {
        margin-left: 5px;
    }
}
br.responsive {
    display: none;
}
.responsive-break {
    display: inline;
}
.nobr {
    white-space: pre;
}
._ibg {
    position: relative;
    img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        object-fit: cover;
    }
}
.row:after {
    display: block;
    content: "";
    clear: both;
}
%noselect {
    user-select: none;
}
%center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.swal2-styled {
    &.swal2-confirm {
        background-color: #597fae;
    }
}
body {
    .swal2-icon.swal2-success [class^=swal2-success-line] {
        background-color: rgba(#597fae, .8);
    }
    .swal2-icon.swal2-success .swal2-success-ring {
        border-color: rgba(#597fae, .3);
    }
    .swal2-styled.swal2-confirm {
        background-color: #597fae;
        &:focus {
            box-shadow: 0 0 0 3px rgba(#597fae,.5)
        }
    }
}
