.dynamic-select {
    display: flex;
    align-items: center;
    flex: auto;
    flex-wrap: wrap;
    position: relative;
    .options {
        visibility: hidden;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 50;
        &-open {
            visibility: visible;
        }
        .option {
            white-space: nowrap;
        }
    }
    .select-shadow-wrapper {
        white-space: nowrap;
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        &[aria-invalid="true"] {
            border-color: red;
        }
        .selected-option {
            cursor: pointer;
            position: relative;
            width: 100%;
            &[value=""] {
            }
            &::after {
                content: "";
                @include transition;
                font-family: icons;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
            }
            &-open {
                &::after {
                    transform: translateY(-50%) rotateX(180deg);
                }
            }
        }
        .options {
            width: 100%;
            margin-left: 0;
            margin-top: 0;
            background-color: #fff;
            outline: 1px solid #fff;
            .option {
                padding: rem(12) 5%;
            }
            @media (any-hover: hover) {
                .option:not([data-value=""]) {
                    &:hover {
                        background-color: rgba(#597fae, .5);
                    }
                }
            }
        }
        .option {
            width: 100%;
            cursor: pointer;
            display: flex;
            align-items: center;
            transition: background-color .3s;
            &[data-selected] {
            }
            &[data-value=""] {
                opacity: 0.5;
            }
        }
    }
    select {
        font-size: inherit;
        option {
        }
    }
}
