.special-offers {
    @include avalue('padding-bottom', 80, 40);
    // .special-offers__container
    &__container {
    }
    &__title {
        .responsive-break {
            @media (max-width: 767px) {
                display: block;
            }
        }
    }
    // .special-offers__offer-list
    &__offer-list {
        display: grid;
        grid-template: auto / repeat(2, 1fr);
        gap: 44px 42px;
        @media (max-width: 992px) {
            grid-template: auto / auto;
        }
        @media (max-width: 480px) {
            width: calc(100% + 18px * 2);
            margin: 0 -18px;
            gap: 20px;
        }
    }
    // .special-offers__offer-item
    &__offer-item {
        min-height: 355px;
        @media (max-width: 992px) {
            min-height: 444px;
        }
        @media (max-width: 480px) {
            min-height: auto;
        }
    }
    .offer {
        color: #fff;
        padding: 52px 76px;
        font-size: 26px;
        line-height: math.div(37.5, 26);
        font-weight: 600;
        border-radius: 29px;
        position: relative;
        overflow: hidden;
        &-large {
            grid-row: span 2;
            @media (max-width: 992px) {
                grid-row: auto;
                gap: 41px;
            }
        }
        @media (max-width: 1440px) {
            padding: 30px 50px;
        }
        @media (max-width: 480px) {
            padding: 30px 20px;
            border-radius: 10px;
            font-size: 16px;
        }
        &__content {
            max-width: 66.666%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            height: 100%;
            @media (max-width: 480px) {
                max-width: none;
            }
        }
        [class*="-price"] {
            padding: 14px 24px;
            text-align: center;
            border-radius: 13px;
            @media (max-width: 1440px) and (min-width: 993px) {
                font-size: 20px;
            }
            @media (max-width: 480px) {
                font-size: 14px;
            }
        }
        // .offer__current-price
        &__current-price {
            background-color: #597fae;
            box-shadow: 0px 0px 35px rgba(#597fae, 0.65);
        }
        // .offer__old-price
        &__old-price {
            text-decoration: line-through;
            color: rgba(#fff, 0.47);
            margin-bottom: auto;
            font-weight: 300;
        }
        // .offer__title
        &__title {
            text-transform: uppercase;
            margin-bottom: 10px;
            font-weight: 600;
            @media (max-width: 480px) {
                margin-top: 40px;
            }
        }
        // .offer__link
        &__link {
            font-size: 16px;
            line-height: math.div(20, 16);
            text-decoration: 1px underline dashed;
            text-underline-position: under;
            text-underline-offset: 3px;
            font-weight: 300;
            &::first-letter {
                text-transform: uppercase;
            }
            @media (max-width: 1440px) {
                font-size: 14px;
            }
        }
    }
}
