.popup {
    transition: opacity 0.3s, background-color 0.5s;
    opacity: 0;
    position: fixed;
    position: fixed;
    pointer-events: none;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    visibility: hidden;
    align-items: center;
    justify-content: center;
    padding: 30px;
    font-size: 24px;
    @media (max-width: 1280px) {
        font-size: 20px;
    }
    @media (max-width: 1024px) {
        font-size: 18px;
    }
    @media (max-width: 768px) {
        font-size: 16px;
        padding: 15px;
    }
    @media (max-width: 480px) {
        font-size: 14px;
        padding: 0;
    }
    &-visible {
        z-index: 130;
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
    @media (max-width: 480px) {
        padding: 0;
        font-size: 18px;
    }
    &-content {
        position: relative;
        height: 100%;
        max-width: 768px;
        overflow: visible;
    }
    &-inner {
        overflow: auto;
        max-height: 100%;
        width: 100%;
        background-color: #fff;
        padding: 40px 0;
        border-radius: 20px;
        @media (max-width: 1280px) {
            border-radius: 15px;
        }
        @media (max-width: 900px) {
            padding: 50px 0 20px;
        }
        @media (max-width: 768px) {
            border-radius: 10px;
        }
        @media (max-width: 480px) {
            border-radius: 0;
        }
    }
    &-header {
    }
    .popup-close-button {
        border-radius: 50%;
        background-color: #fff;
        margin-left: auto;
        position: absolute;
        top: 10px;
        right: 10px;
        @include size(50);
        transform: translate(50%, -50%);
        border: 2px solid #ccc;
        cursor: pointer;
        transition: border-color .3s;
        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            width: 2px;
            height: 70%;
            background-color: #bbb;
            transition: background-color .3s;
        }
        &::before {
            transform: translate(-50%, -50%) rotate(45deg);
        }
        &::after {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
        @media (any-hover: hover) {
            &:hover {
                border-color: #666;
                &::before, &::after {
                    background-color: #666;
                }
            }
        }
        @media (max-width: 9992.02px) {
            transform: translate(-30%, 15%);
        }
        @media (max-width: 480px) {
            @include size(40);
        }
    }
    &-container {
        height: 100%;
        @include flex-center;
    }
    &-body {
        max-width: $maxWidthContainer + px;
        margin: 0 auto;
        overflow: auto;
        height: 100%;
        padding-bottom: 30px;
    }
}
button.close-button {
    width: 20px;
    height: 20px;
    cursor: pointer;
    transition: all 0.5s ease;
    font-size: 0;
    line-height: 0;
    margin: 0;
    padding: 0;
    position: relative;
    align-self: flex-start;
    flex-grow: 0;
    &:hover {
        // background-color: rgba(50, 50, 50, 0.05);
        // transform: scale(1.4);
        // border: 1px solid;
        // border-radius: 50%;
    }
    &::before {
        content: "";
        display: block;
        width: 1px;
        height: 20px;
        position: absolute;
        width: 1px;
        height: 20px;
        top: 0;
        left: 50%;
        background-color: #07273e;
        transform: rotate(45deg);
    }
    &::after {
        content: "";
        display: block;
        width: 1px;
        height: 20px;
        position: absolute;
        top: 0;
        left: 50%;
        width: 1px;
        height: 20px;
        background-color: #07273e;
        transform: rotate(135deg);
    }
}
